@import "libs";
@import "reset";
@import "vars";
@import "sprite";
// Методом проб и ошибок были получены именно такие значения. При этом каждое значение из набора имеет свой логический смысл:
//
// 0.3rem: малый border-radius, некоторые технические вещи вроде подчеркиваний
// 0.5rem: большой border-radius, горизонтальные отступы между иконкой и текстом, некоторые отступы между шрифтами разного размера в заголовках
// 0.8rem: малый размер шрифта (теги, даты постов, подписи и.т.д.)
// 1rem: эталонный шрифт, вертикальный и горизонтальный отступ по умолчанию для всего, что только есть (включая отступы в сетке, разумеется)
// 1.25rem: увеличенный шрифт
// 1.5rem: увеличенный шрифт для больших заголовков
// 1.75rem: увеличенный шрифт для больших заголовков (заменяет 1.5 в случае некоторых шрифтов)
// 2rem: большой шрифт, большие отступы во всех направлениях
// 3rem: большой вертикальный отступ
// 4rem: большой отступ между секциями
// 5rem: большой-большой отступ между секциями (имеет смысл в лендингах, когда на стыке секций в верхней отступ делается больше чем в нижней)
:root {
    font-family: 'Montserrat', sans-serif;
    font-size: calc( 12px + (20 - 12) * ((100vw - 400px) / ( 1200 - 400)));

    * {
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        -webkit-overflow-scrolling: touch;
    }
}
.jfjk {
  font-size: 6rem;
}

